.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}




.prooftable, .prooftable th, .prooftable td {
  border: 1px solid #ddd;
}

.prooftable {
  border-collapse: collapse;
  width: 100%;
  border: 3px solid #ddd;
}

.prooftable th, .prooftable td {
  text-align: left;
  padding: 5px;
}

/* .prooftable tr:nth-child(even) {
  background-color: #f2f2f2;
} */


.colon {
  padding-left: 5px;
  padding-right: 5px;
}

.assumption {
  width: 100%;
}

.rule-choice {
  text-align: center;
  justify-content: center;
}

.rule-choice > div {
}

.finished {
  background-color: #266239;
}

.selected {

}

/* 
  last row with a div child (in td) with css class .selected 
*/
.prooftable tr:last-child :has(> div.selected) {
  background-color: #ff0000;
}